.container{
    width: 50%;
    padding: 0.2em;
}
.container.grid {
    width: 50%;;
}
.container.list{
    width: 100%;;
}
.video {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    border: 1px solid lightgray;
    cursor: pointer;
    transition: transform 250ms ease-in;
}
.video:hover {
    transform: scale(1.02);
}

.thumbnail{
    width: 40%;
    height: 100%;
}

.metadata{
    margin-left: 0.2em;
}
.title,
.channel {
    margin: 0;
    font-size: 0.8rem;
}

.channeltitle {
    font-size: 0.6rem;
}